import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';

const QuestionPreview = ({ question }) => {
  if (!question || !question.content) {
    return (
      <div className="container py-4">
        <div className="alert alert-info text-center">
          Cha c cu hi no  xem trc.
        </div>
      </div>
    );
  }

  return (
    <div className="container py-4">
      <h2 className="mb-4 text-center">Xem trc cu hi</h2>
      <div className="card shadow-sm">
        <div className="card-body">
          <div className="d-flex flex-wrap gap-2 mb-3 align-items-center">
            <span className="badge bg-secondary">
              {question.type === 'multiple-choice' ? 'Trc nghim' : 'T lun'}
            </span>
            <span className="badge bg-light text-secondary border">
              {question.category === 'chuyen-mon' ? 'Chuyn mn' : 'C s'}
            </span>
          </div>
          <p className="fs-5 mb-4">{question.content}</p>

          {question.type === 'multiple-choice' && (
            <>
              <ul className="list-group list-group-flush">
                {question.options?.map((option) => (
                  <li
                    key={option.id}
                    className={`list-group-item d-flex justify-content-between align-items-center ${
                      option.isCorrect ? 'list-group-item-success' : ''
                    }`}
                  >
                    <span>{option.text}</span>
                    {option.isCorrect && <span className="badge bg-success">p n ng</span>}
                  </li>
                ))}
              </ul>

              {(question.explanation || question.explanationUrl) && (
                <div className="mt-4 p-4 rounded-4 border border-1" style={{ background: 'rgba(248, 250, 252, 0.9)' }}>
                  <h6 className="fw-semibold text-secondary text-uppercase mb-3" style={{ letterSpacing: '0.05em' }}>
                    Thng tin b sung
                  </h6>
                  <div className="d-flex flex-column gap-3">
                    {question.explanation && (
                      <div>
                        <span className="d-block text-muted small text-uppercase mb-1">Gii thch cu hi</span>
                        <p className="mb-0 text-body-secondary">{question.explanation}</p>
                      </div>
                    )}
                    {question.explanationUrl && (
                      <div>
                        <span className="d-block text-muted small text-uppercase mb-1">Lin kt gii thch</span>
                        <a
                          href={question.explanationUrl}
                          target="_blank"
                          rel="noopener noreferrer"
                          className="link-primary text-break"
                        >
                          {question.explanationUrl}
                        </a>
                      </div>
                    )}
                  </div>
                </div>
              )}
            </>
          )}

          {question.type === 'essay' && (
            <div className="alert alert-secondary mt-3">
              <strong>Gi :</strong> {question.hint || 'Khng c'}
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default QuestionPreview;